.phone {
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
  text-decoration: none;
  color: var(--basic-background);

  @media (min-width: $medium-width) {
    font-size: 24px;
    line-height: 24px;
  }

  @media (min-width: $large-width) {
    font-size: 30px;
    line-height: 30px;
  }
}
